@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&family=Edu+AU+VIC+WA+NT+Dots:wght@400..700&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-behavior: smooth;
}

html, body {
  height: 100%;
  background-color: black; 
  color: white; 
}

h1 { 
  @apply text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-orange-400 to-pink-600 inline-block; 
  font-family: 'Dancing Script', cursive;
}

h2 {
  @apply text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-orange-400 to-pink-600 inline-block; 
  font-family: 'Dancing Script', cursive;
}

.my-custom-class {
  @apply text-pink-800; 
}

.bg-primary-color {
  @apply px-1 py-2 w-full rounded-xl mr-4 bg-gradient-to-r from-orange-500 to-pink-500 text-black font-bold hover:border-none; 
}

.navbar, .footer {
  background-color: black;
  padding: 1rem; /* Adjusted padding */
}

section {
  min-height: 100vh; /* Ensures sections fill the full height of the viewport */
  padding-top: 2rem;  /* Adjust padding */
  padding-bottom: 2rem; /* Adjust padding */
}

.flex-container {
  @apply flex flex-col min-h-screen; /* Flex container for navbar, content, footer */
}
